


















export default {
	data() {
		return {
			latitude: '',
			longitude: '',
			mapContext: {}
		};
	},
	onReady(){
		this.mapContext = uni.createMapContext('map',this)
		this.changePosition()
	},
	methods: {
		changePosition(){
			this.mapContext.moveToLocation()
		},
		openScan(){
			uni.scanCode({
				onlyFromCamera: true,
				success: (res) => {
					this.navicateToPath(res.path)
				}
			});
		},
		navicateToPath(url){
			uni.navigateTo({
				url: '/'+ url
			});
		},
		navicateToSendCard(){
			uni.navigateTo({
				url: '../sendCard/sendCard'
			});
		}
	}
};
